

export const ifExits = ( prop, obj, rtn='' ) => {

     const exits = (data, array, rtn) => {
          if( array.length > 1 ){
               return (
                    data[`${array[0]}`] ?
                         exits( data[`${array[0]}`], array.slice(1,), rtn)
                         : rtn
               )
          }else{
               return(
                    data[`${array[0]}`] ?
                         data[`${array[0]}`]
                         : rtn
               )
          }
     };

     if( Array.isArray(prop) ){
          return(
               exits(obj,prop,rtn)
          );

     }else{
          return(
               obj[`${prop}`] ? String(obj[prop]) : '--'
          )
     }

};
