
import React, {useState} from 'react' ;
import { styled } from '@mui/system';
import { Hint } from './Hint' ;

import { reLoadResources } from '../../../recursosFHIR/helpers/reLoadResources';


import { MediaQuery } from '../../../MediaQuery'


export const TextInput = ({
          style={},
          placeHol='',
          field = ' ',
          width = '20',
          type = 'text',
          disable = false,
          dataOut={},
          mltLine = false,
          maxRow = 1,
          save = true,
          fullwidth = false,
          help='',
          setChange={}
     }) => {


     const { _xl, _lg } = MediaQuery();

     const Container  =  styled('div')({
          width: fullwidth ?( _xl? '98%': _lg? '98%':'97%') : '',
          resize: 'none',
          fontFamily: 'Montserrat !important',
          textAlign: 'center',
          marginTop: help.flag ? '0.38rem' : '0.5rem',
          marginBottom: '0.5rem',
          marginRight: _xl? '0.5rem': _lg? '0.5rem':'0.5rem',
          display:'flex',
          flexDirection: 'column',
          alignItems: 'flex-start',
     });

     const FieldText = styled('div')({
          display: 'flex',
          alignItems: 'center',
          color: disable ? 'rgb(33 33 33)' : '#0d47a1',
          position: 'relative',
          top: '0.3rem;',
          fontSize: _xl ? '25px' : _lg ? '20px': '14px',
          background: 'white;',
          paddingInline: '0.3rem;',
          fontWeight: 'bold',
          lineHeight: '100%;',
          marginLeft:'0.6rem'
     });

     const styleText = {
          width: fullwidth ?( _xl? '98%': _lg? '99%':'99%') : '',
          paddingBlock: '0.5rem',
          fontWeight: '500',
          fontFamily: 'Montserrat !important',
          border:'0.13rem solid #c4c4c4',
          borderRadius: '0.3rem',
          paddingInline: type==='date' ? '0.55rem' : '0.2rem',
          transition: 'border 0.3s ease-in',
          outline: '0 none',
          fontSize: _xl ? '22px' : _lg ? '16px': '12px',
          lineHeight: _xl ? '1.5rem' : _lg ? '1.1rem': '0.8rem !important',
          '&:hover': {
               border: disable ? '' : '0.13rem solid rgb(33 33 33)'
          },
          '&:focus': {
               border: '0.13rem solid rgb(13 71 161) !important'
          },
          '&:active': {
               border: '0.13rem solid rgb(13 71 161) !important'
          },
     };

     const InputArea = styled('textarea')({
          ...styleText,
          paddingLeft:'0.5rem',
          textAlign:'left',
          resize: 'none'
     }) ;

     const Input = styled('input')({
          ...styleText,
          textAlign:'center'
     }) ;

     dataOut = save ?  JSON.parse( localStorage.getItem('Data') ) : dataOut ;

     const dta = JSON.parse(localStorage.getItem('Data')) ;
     const addCero = (n) => parseInt(n) < 10 ? `0${n}` : n ;
     const dateMinor = `${dta['Año']}-${dta['Mes']}-${dta['Día']}` ;
     const dateMajor =  new Date().toLocaleString()
                              .split(',')[0].split('/')
                              .reverse().map( num => addCero(num) ).join('-') 

     const BodyText = () =>{

          const [value, setValues] = useState({
               state: dataOut?.[`${field}`] ? dataOut[`${field}`] : ' '
          }) ;

          const handleChange = ( event ) => {
               event.preventDefault();
               value.state = ( event.target.value )

               if( field === 'Fecha' & value.state.length > 0){
                    reLoadResources( value.state, setChange );  // Recargar recursos del FHIR, ajustados a la fecha establecida.
               };

               setValues(
                    { ...value }
               );
          };

          const handleBlur = ( event ) => {

               dataOut = save ?  JSON.parse( localStorage.getItem('Data') ) : dataOut ;

               dataOut[`${field}`] = event.target.value  ;

               if(dataOut[`${field}`].length < 1)  delete dataOut[`${field}`] ;

               if(save) localStorage.setItem( 'Data', JSON.stringify(dataOut, null, 2) ) ;

          };

          if ( mltLine ){
               return(
                    <InputArea
                         type={type}
                         size={width}
                         value={ disable ? ' ' : value.state }
                         onChange={ (event) => handleChange(event) }
                         onBlur={ (event) => handleBlur(event) }
                         onKeyDown={ (event) => handleBlur(event)}
                         disabled={disable}
                         rows={maxRow}
                         query={MediaQuery()}
                    />
               )
          };

          if( type === 'date' ){
               return(
                    <Input
                         style={style}
                         type={type}
                         size={width}
                         min={dateMinor}  // "2018-01-01"
                         max={dateMajor}   // "2018-01-01"
                         value={disable ? placeHol : value.state }
                         onChange={  (event) => handleChange(event)  }
                         onBlur={  (event) => handleBlur(event) }
                         onKeyDown={ (event) => handleBlur(event)}
                         disabled={disable}
                         query={MediaQuery()}
                         placeholder={placeHol}
                    />
               );
          }

          return(
               <Input
                    style={style}
                    type={type}
                    size={width}
                    value={disable ? placeHol : value.state }
                    onChange={  (event) => handleChange(event)  }
                    onBlur={  (event) => handleBlur(event) }
                    onKeyDown={ (event) => handleBlur(event)}
                    disabled={disable}
                    query={MediaQuery()}
                    placeholder={placeHol}
               />
          );
     };

     return(
          <Container>

               <FieldText>
                    {field}

                    <Hint help={help}/>

               </FieldText>

               <BodyText/>

          </Container>
     );
}







