import useMediaQuery from '@mui/material/useMediaQuery';

// value         |0px     600px    960px    1280px   1920px
// key           |xs      sm       md       lg       xl
// screen width  |--------|--------|--------|--------|-------->
// range         |   xs   |   sm   |   md   |   lg   |   xl



export const MediaQuery = () => {

     const match_xl = useMediaQuery('(min-width:2500px)');
     // const match_lg = useMediaQuery(theme => theme.breakpoints.up('lg'));
     const match_lg = useMediaQuery('(min-width:1500px)');

     return ({
          _xl: match_xl,
          _lg: match_lg
     });

}

