

import { ifExits } from './ifExits'; // idExits( 'prop', obj )
import { imageDefault } from './imageDefault';

export const loadPatient = (data, exactDate=false) => {

     let sexo = 'unknown'
     const stringSexo = ifExits('Sexo', data);

     ['mas', 'Mas'].forEach((item) => {
          if (stringSexo.includes(item)) {
               sexo = 'male'
          };
     });

     ['fem', 'Fem'].forEach((item) => {
          if (stringSexo.includes(item)) {
               sexo = 'female'
          };
     });

     return (
          {
               resourceType: 'Patient',
               identifier: [
                    {
                         use: 'official',
                         value: ifExits('Cuenta corriente', data)
                    },
                    // {
                    //      use: "secondary",
                    //      value: identifier2 // hospitalization_id
                    // }
               ],
               extension: [
                    {
                         url: "urn:uuid:53fefa32-fcbb-4ff8-8a92-55ee120877b7",
                         valueString: ifExits('Estado de bienestar (0-3)', data),
                    }
               ],
               active: true,
               name: [
                    {
                         use: 'official',
                         text: ifExits('Nombres y apellidos', data)
                    }
               ],
               birthDate: exactDate ? ifExits('Edad', data): `${new Date().getFullYear() - ifExits(['Edad'], data, 30)}-10-10`,
               gender: sexo,
               address: [
                    {
                         text: ifExits('Cama', data),
                         district: ifExits('Ala', data),
                         state: ifExits('Sala', data),
                    }
               ],
               photo: [
                    {
                         "contentType": "image/gif",
                         "data": ifExits('Foto', data) === '--' ? imageDefault : ifExits('Foto', data)
                    }
               ]
          }
     )
};
