
import React, {useState} from 'react' ;
import { styled } from '@mui/system';
import { Hint } from './Hint' ;


import { MediaQuery } from '../../../MediaQuery'


export const TextInputOpt = ({
          field = ' ',
          width = '20',
          type = 'text',
          disable = false,
          dataOut={},
          // maxRow = 1,
          save = true,
          fullwidth = false,
          help='',
          opt=[],
          blur=()=>null
     }) => {

     const { _xl, _lg } = MediaQuery();

     const Container  =  styled('div')({
          position:'relative',
          width: fullwidth ?( _xl? '98%': _lg? '99%':'99%') : '',
          resize: 'none',
          fontFamily: 'Montserrat !important',
          textAlign: 'center',
          marginTop: help.flag ? '0.38rem' : '0.5rem',
          marginBottom: '0.5rem',
          marginRight: _xl? '0.9rem': _lg? '0.7rem':'0.55rem',
          display:'flex',
          flexDirection: 'column',
          alignItems: 'flex-start',
     });

     const FieldText = styled('div')({
          display: 'flex',
          alignItems: 'center',
          color: disable ? 'rgb(33 33 33)' : '#0d47a1',
          position: 'relative',
          top: '0.3rem;',
          fontSize: _xl ? '25px' : _lg ? '20px': '14px',
          background: 'white;',
          paddingInline: '0.3rem;',
          fontWeight: 'bold',
          lineHeight: '100%;',
          marginLeft:'0.6rem'
     });

     const Opt = styled('div') ({
          width:'100%',
          cursor:'pointer',
          background:'rgb(0,0,250,5%)',
          padding:'2px',
          marginBottom:'0.5rem',
          borderRadius:'3px',
          '&:hover':{
               background:'rgb(0,0,250,20%)'
          }
     });

     const Input = styled('div')({
          // position:'absolute',
          width: fullwidth ? ( _xl? '98%': _lg? '99%':'99%') : `${width*265/30}px` ,
          display:'flex',
          flexDirection:'column',
          textAlign:'center',
          fontWeight: '500',
          fontFamily: 'Montserrat !important',
          border:'0.13rem solid #c4c4c4',
          borderRadius: '0.3rem',
          transition: 'border 0.3s ease-in',
          outline: '0 none',
          fontSize: _xl ? '22px' : _lg ? '16px': '12px',
          lineHeight: _xl ? '1.5rem' : _lg ? '1.1rem': '0.8rem !important',
          '&:hover': {
               border: disable ? '' : '0.13rem solid rgb(33 33 33)'
          },
          '&:focus': {
               border: '0.13rem solid rgb(13 71 161) !important'
          },
          '&:active': {
               border: '0.13rem solid rgb(13 71 161) !important'
          },
     }) ;


     const BodyText = () =>{

          const [value, setValues] = useState({
               state: dataOut?.[`${field}`] ? dataOut[`${field}`] : ''
          });

          const [disp, setDisp] = useState(false);

          const handleClick = ( event ) => {
               event.preventDefault() ;

               setValues({
                    state : event.target.outerText.replace(/[ ]/g,'') 
               });
               
               dataOut = save ?  JSON.parse( localStorage.getItem('Data') ) : dataOut ;
               
               dataOut[`${field}`] = event.target.outerText  ;
               
               if(save) localStorage.setItem('Data', JSON.stringify(dataOut, null, 2)) ;

               blur( event );
          };


          return(
               <Input
                    onClick={()=>setDisp(!disp)}
               >
                    <div
                         style={{display:disp ? 'none' : 'block', marginBlock: '0.5rem',height:'1rem' }}
                    >
                         {value.state ? value.state : ''}
                    </div>

                    <div
                         style={{display:disp ? 'block' : 'none', marginBlock: '0.5rem'}}
                    >
                         {
                              opt.map( (item,idx) =>
                                   <Opt key={idx} onClick={(event)=>handleClick(event)} >
                                        {item}
                                   </Opt>
                              )
                         }
                    </div>
               </Input>
          );
     };

     return(
          <Container>

               <FieldText>
                    {field}

                    <Hint help={help}/>

               </FieldText>

               <BodyText/>

          </Container>
     );
}







