
import React, {useState} from 'react' ;
import HelpIcon from '@mui/icons-material/Help';

import { MediaQuery } from '../../../MediaQuery'

export const Hint = ({help}) => {

     const { _xl, _lg } = MediaQuery();

     const [disp, setDisp] = useState(
          false
     );


     return(
          <>
               {
                    help &&
                    <HelpIcon
                         fontSize="small"
                         style={{cursor:'pointer', marginLeft:'0.3rem'}}
                         onClick={ () => setDisp( !disp ) }
                    />
               }
               <div
                    onClick={ () => setDisp( !disp ) }
                    style={{
                         display:disp ? 'block' : 'none',
                         position:'absolute',
                         padding: '0.3rem',
                         border:'2px solid rgb(13, 71, 161)',
                         borderRadius:'5px',
                         background:'white',
                         cursor:'pointer',
                         boxShadow:'4px 4px 2px rgb(100,100,100,75%)'
                    }}
               >
                    <span
                         style={{fontFamily:'Montserrat', fontSize: _xl ? '22px' : _lg ? '16px': '12px', color: 'rgb(60,60,60,75%)'}}
                    >
                         { help ? help: '' }
                    </span>
               </div>
          </>
     );
};
