

import { getFromServer } from '../getFromServer' ;

export const reLoadResources = ( date, set ) => {

     console.log( date ) ;

     getFromServer(
          JSON.parse( localStorage.getItem('cntPatient') ),
          date //"2022-12-19"
     ).then( ( {data, idPatient}) =>{

          data.Fecha = date ;
          localStorage.setItem('Data', JSON.stringify( data, null, 2) ) ;
          console.log( "Recursos Actualizados" );

          set(`Changed:${date}`);
          // const  baseUrl = window.location.href.split('/coc/')[0]
          // window.location.href = `${baseUrl}/coc/panel/uci/formularioEnfermeria` ;

     });
};
